//** Width of the `border` for generating carets that indicate dropdowns.
@plone-caret-width-base:          4px;
//** Carets increase slightly in size for larger components.
@plone-caret-width-large:         5px;
@plone-font-size-base:           16px;

// a { outline: 1px dotted white; margin: 0.25em; }
.plone-navbar-nav li {
  &:hover > .submenu {
    display: block;
  }
  &.active:after,
  &:hover:after {
    content: "";
    display: block;
    position: absolute;
    bottom: -10px;
    width: 100%;
    z-index: 999;
  }
  .submenu {
    background-color: @plone-link-color;
    display: none;
    list-style: none;
    padding-left: 0;
    padding-bottom: 0;
    position: absolute;
    z-index: 9999;
    a {
      color: white;
      display: block;
      line-height: 20px;
      min-width: 14em;
      padding: @plone-font-size-base;
      position: relative;
      &:hover {
        background-color: #9abdd6;
        text-decoration: none;
      }
      &:before {
        margin-right: 10px;
      }
      .submenu_image {
        float: right;
      }
    }
    // .dropdown-submenu a.hasDropDown {
    //   &:after {
    //     content: "▸";
    //     display: inline-block;
    //     position: absolute;
    //     right: 0;
    //   }
    // }
    li {
      position: relative;
      float: left;
      &:hover ul {
        top: 0;
        left: 100%;
      }
    }
  }
}

.opener {
  background: transparent;
  border: 1px solid white;
  float: right;
  text-align: center;
  width: @plone-font-size-base * 2;
  height: @plone-font-size-base * 2;
  margin-top: -(@plone-font-size-base * 0.4);
  margin-right: -(@plone-font-size-base * 0.7);
  .caret {
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: 2px;
    vertical-align: bottom;
    border-top:   0.5em dashed;
    border-right: 0.5em solid transparent;
    border-left:  0.5em solid transparent;
  }
}
.openmenu { display: block!important; }

@media (max-width: 768px) {
.plone-navbar-collapse.in {
  .plone-navbar-nav {
      margin: 0 -15px 0;
  }
  li {
    &:hover > .submenu {
      display: none;
    }
    .submenu {
      // display: block;
      position: relative;
      a {
        &:hover {
          // background-color: unset;
        }
      }
      li {
        float: none;
        &:hover ul {
            top: unset;
            left: unset;
        }
      }
    // .dropdown-submenu a.hasDropDown {
    //   &:after {
    //     content: "▸";
    //     display: inline-block;
    //     position: absolute;
    //     background: red;
    //     cursor: hand;
    //     z-index: 1;
    //     width: 2em;
    //     height: 2em;
    //     right: 0;
    //   }
    // }

    }
    .navTreeLevel0 .submenu_title {
      padding-left: 0.5em;
    }
    .navTreeLevel1 .submenu_title {
      padding-left: 1em;
    }
    .navTreeLevel2 .submenu_title {
      padding-left: 1.5em;
    }
    .navTreeLevel3 .submenu_title {
      padding-left: 2em;
    }
  }
}
  .plone-navbar-nav li {
    .submenu {
      // display: none;
    }
    &:hover > .submenu {
      // display: block;
    }
  }
}

